import React from 'react';
import styles from './Projects.module.css';
import Zoom from 'react-reveal/Zoom';
import Project from "./Project/Project";
import Title from '../Title/Title';
import imageStore from '../../img/store.jpg'
import imageList from '../../img/todolist.jpg'
import imageNetwork from '../../img/networck.jpg'
import imageLayaut from '../../img/adaptiv_layout.JPG'
import imageNotes from '../../img/notes.jpg'

function Projects() {

	const backStore = {backgroundImage: `url(${imageStore})`}
	const backList = {backgroundImage: `url(${imageList})`}
	const backNetwork = {backgroundImage: `url(${imageNetwork})`}
	const adaptiv_layout = {backgroundImage: `url(${imageLayaut})`}
	const notes = {backgroundImage: `url(${imageNotes})`}

  return (
    <div id="projects" className={styles.projects}>
	    <Zoom>
		    <div className={styles.container}>
			    <Title title={"My projects"}/>
			    <div className={styles.projectWrapper}>
				    <Project title={"Store Games"} style={backStore}
				             toGo={`https://store-games-express.herokuapp.com/`}
				             description={`The following technology stack was used in this project:
				     Node.js (Express), Handlebars, MongoDB, Mongoose, Heroku`}/>
				    <Project title={"Social Network"} style={backNetwork}
				             description={`Project in development ...`} />
				    <Project title={"To do list"} style={backList}
				             description={`Project in development ...`}/>
				    <Project title={"Adaptiv layout"} style={adaptiv_layout}
				             toGo={`https://diegoshe.github.io/test-01-adaptiv-layout/`}
				             description={`Full-screen page, the resolution of the monitors fits into the screen without the appearance of a scroll. The image on the background illustrates a slider of several images that seamlessly replace each other. The menu and search buttons open the navigation bar and the search bar, respectively. 
				             All effects on the page are smooth without sudden jumps and twitches.`}/>
				    <Project title={"Notes"} style={notes}
				             toGo={`https://diegoshe.github.io/notes/`}
				             description={`The application is written in React, using context, 
				             hooks, bootstrap, firebase.`}/>
			    </div>
		    </div>
	    </Zoom>
    </div>
  );
}

export default Projects;
